.experience {
    background: transparent;
    border: none;
    box-shadow: none;
    padding: 0;
}

.experience .container {
    width: 100%;
}

.experience h2 {
    margin-bottom: 0.5rem;
}

.experience .timeline {
    display: grid;
    gap: 1.5rem;
    margin-top: 2rem;
    position: relative;
}

.experience .timeline::before {
    content: '';
    position: absolute;
    left: 20px;
    top: 0;
    bottom: 0;
    width: 2px;
    background: linear-gradient(var(--accent), var(--accent-2));
    opacity: 0.6;
}

.experience .timeline-item {
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 20px;
    padding: 1.75rem;
    box-shadow: var(--shadow);
    position: relative;
    padding-left: 3.5rem;
}

.experience .timeline-item::before {
    content: '';
    position: absolute;
    left: 10px;
    top: 1.5rem;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background: var(--bg);
    border: 3px solid var(--accent);
}

.experience .timeline-item h3 {
    margin-bottom: 0.35rem;
}

.experience .timeline-item h4 {
    font-weight: 500;
    color: var(--accent-2);
    margin-bottom: 0.5rem;
}

.experience .timeline-item p {
    margin-bottom: 0.4rem;
    color: var(--muted);
}

.experience ul {
    margin-top: 0.5rem;
    padding-left: 1rem;
    color: var(--text);
}

.experience li {
    margin-bottom: 0.45rem;
    line-height: 1.5;
}

@media (max-width: 768px) {
    .experience .timeline::before {
        left: 10px;
    }

    .experience .timeline-item {
        padding-left: 2.5rem;
    }

    .experience .timeline-item::before {
        left: 0;
    }
}
