/* Publications.css */

.publications {
    background: transparent;
    border: none;
    box-shadow: none;
    padding-bottom: 0;
}

.publications .container {
    width: 100%;
}

.publications h2 {
    margin-bottom: 0.5rem;
}

.publications > .container > p {
    color: var(--muted);
    margin-bottom: 2rem;
}

.publication-card {
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 24px;
    padding: 1.75rem;
    margin-bottom: 1.5rem;
    box-shadow: var(--shadow);
}

.publication-card h3 {
    margin-bottom: 0.4rem;
    color: var(--accent-2);
}

.publication-card p {
    color: var(--text);
    margin-bottom: 0.35rem;
}

.publication-link {
    display: inline-flex;
    align-items: center;
    gap: 0.4rem;
    margin-top: 0.4rem;
    color: var(--accent);
    text-decoration: none;
    font-weight: 600;
}

.publication-link:hover {
    color: var(--accent-2);
}
