.about-section {
    background: transparent;
    border: none;
    box-shadow: none;
    padding-bottom: 0;
}

.about-title {
    text-align: left;
    font-size: clamp(2rem, 4vw, 2.6rem);
    margin-bottom: 1.5rem;
}

.about-paragraph {
    font-size: 1.05rem;
    margin-bottom: 1rem;
    color: var(--muted);
}

.about-paragraph + .about-paragraph {
    color: var(--text);
}