footer {
    text-align: center;
    margin: 3rem auto 2rem;
    padding: 1.5rem 0;
    color: var(--muted);
}

footer ul {
    list-style: none;
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin: 1rem 0;
}

footer ul li a {
    color: var(--muted);
    text-decoration: none;
    transition: color 0.2s ease;
}

footer ul li a:hover {
    color: var(--accent-2);
}

.social-icons a {
    color: var(--muted);
    font-size: 1.4rem;
    margin: 0 0.4rem;
    transition: color 0.2s ease;
}

.social-icons a:hover {
    color: var(--accent);
}
