header {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    padding: 1rem 0;
    backdrop-filter: blur(18px);
}

nav {
    width: min(1100px, 92%);
    margin: 0 auto;
    padding: 0.6rem 1.25rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgba(148, 163, 184, 0.25);
    border-radius: 999px;
    background: rgba(5, 8, 22, 0.65);
    box-shadow: 0 20px 40px rgba(5, 8, 22, 0.55);
}

nav .logo {
    font-size: 1.15rem;
    font-weight: 600;
    letter-spacing: 0.08em;
}

nav ul {
    display: flex;
    list-style: none;
    gap: 0.5rem;
}

nav ul li a {
    color: #e2e8f0;
    text-decoration: none;
    padding: 0.45rem 0.9rem;
    border-radius: 999px;
    font-size: 0.95rem;
    transition: background 0.25s ease, color 0.25s ease;
}

nav ul li a:hover {
    background: rgba(139, 92, 246, 0.15);
    color: #fff;
}

.header-buttons {
    display: flex;
    gap: 0.5rem;
}

.header-buttons .btn {
    margin: 0;
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
}

@media (max-width: 960px) {
    nav {
        flex-direction: column;
        gap: 1rem;
        border-radius: 24px;
    }

    nav ul {
        flex-wrap: wrap;
        justify-content: center;
    }
}
