.skills {
    background: transparent;
    border: none;
    box-shadow: none;
    padding-bottom: 0;
}

.skills .container {
    width: 100%;
}

.skills h2 {
    margin-bottom: 0.5rem;
}

.skills-wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 1.5rem;
    margin-top: 2rem;
}

.skills-column {
    background: var(--card);
    border-radius: 20px;
    border: 1px solid var(--border);
    padding: 1.5rem;
    box-shadow: var(--shadow);
    min-height: 220px;
}

.skills-column h3 {
    margin-bottom: 0.75rem;
    color: var(--accent-2);
    font-size: 1.05rem;
}

.skills-column ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 0.45rem;
    color: var(--muted);
    font-size: 0.95rem;
}
