.projects {
    background: transparent;
    border: none;
    box-shadow: none;
    padding-bottom: 0;
}

.projects .container {
    width: 100%;
}

.projects .slide-header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 1rem;
    align-items: center;
}

.projects .slide-counter {
    color: var(--muted);
}

.projects .timeline {
    position: relative;
    width: 100%;
    height: 580px;
    overflow: hidden;
    margin-top: 1.5rem;
}

.projects .timeline-item {
    position: absolute;
    inset: 0;
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 24px;
    padding: clamp(1.5rem, 3vw, 2.5rem);
    box-shadow: var(--shadow);
    opacity: 0;
    transform: translateX(30px);
    transition: opacity 0.4s ease, transform 0.4s ease;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.projects .timeline-item.active {
    opacity: 1;
    transform: translateX(0);
}

.projects .slide-content h3 {
    font-size: clamp(1.6rem, 3.5vw, 2rem);
}

.projects .slide-content h3 i {
    margin-right: 0.5rem;
    color: var(--accent);
}

.projects h4 {
    color: var(--accent-2);
    font-weight: 500;
}

.projects .slide-section {
    margin-bottom: 0.75rem;
}

.projects .slide-section h5 {
    color: var(--muted);
    margin-bottom: 0.4rem;
    font-size: 0.95rem;
    letter-spacing: 0.08em;
    text-transform: uppercase;
}

.projects .slide-section p,
.projects .slide-section li {
    color: var(--text);
}

.projects .slide-section ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 0.35rem;
}

.projects .navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    margin-top: 1.5rem;
}

.projects .nav-arrow {
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.15);
    color: #fff;
    padding: 0.6rem 1rem;
    border-radius: 999px;
    cursor: pointer;
    transition: background 0.25s ease, transform 0.25s ease;
}

.projects .nav-arrow:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);
}

.projects .nav-dots {
    display: flex;
    gap: 0.4rem;
}

.projects .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.15);
    cursor: pointer;
    transition: background 0.25s ease, transform 0.25s ease;
}

.projects .dot.active {
    background: linear-gradient(120deg, var(--accent), var(--accent-2));
    transform: scale(1.2);
}

.projects .slide-footer {
    text-align: center;
    margin-top: 1rem;
    color: var(--muted);
}

@media (max-width: 768px) {
    .projects .timeline {
        height: 640px;
    }

    .projects .timeline-item {
        padding: 1.5rem;
    }
}