@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&display=swap');

:root {
  --bg: #050816;
  --bg-soft: #0f172a;
  --card: rgba(15, 23, 42, 0.78);
  --border: rgba(148, 163, 184, 0.25);
  --accent: #8b5cf6;
  --accent-2: #06b6d4;
  --text: #e2e8f0;
  --muted: #94a3b8;
  --shadow: 0 25px 55px rgba(5, 8, 22, 0.65);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Space Grotesk', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: radial-gradient(circle at top, rgba(36, 64, 147, 0.45), transparent 45%),
              radial-gradient(circle at 8% 20%, rgba(139, 92, 246, 0.3), transparent 40%),
              radial-gradient(circle at 80% 0%, rgba(6, 182, 212, 0.25), transparent 35%),
              var(--bg);
  color: var(--text);
  line-height: 1.7;
  min-height: 100vh;
  overflow-x: hidden;
}

.App {
  padding-top: 90px;
}

.container {
  width: min(1100px, 92%);
  margin: 0 auto;
}

section {
  width: min(1100px, 92%);
  margin: 0 auto 3.5rem;
  padding: clamp(2rem, 5vw, 3.5rem);
  background: var(--card);
  border-radius: 28px;
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
  backdrop-filter: blur(18px);
}

h1, h2, h3, h4 {
  color: #f8fafc;
  font-weight: 600;
}

p {
  color: var(--text);
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1.5rem;
  border-radius: 999px;
  background: linear-gradient(120deg, var(--accent), var(--accent-2));
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  border: 1px solid transparent;
  box-shadow: 0 12px 32px rgba(107, 70, 193, 0.45);
  transition: transform 0.25s ease, box-shadow 0.25s ease, opacity 0.25s ease;
}

.btn.ghost {
  background: transparent;
  border-color: rgba(248, 250, 252, 0.35);
  color: var(--text);
  box-shadow: none;
}

.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 18px 36px rgba(107, 70, 193, 0.55);
}

.btn.ghost:hover {
  background: rgba(248, 250, 252, 0.08);
  box-shadow: none;
}

.section-title {
  font-size: clamp(1.8rem, 4vw, 2.4rem);
  margin-bottom: 0.35rem;
}

.section-subtitle {
  color: var(--muted);
  margin-bottom: 2rem;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #0b1221;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(var(--accent), var(--accent-2));
  border-radius: 999px;
}